<template>
  <div class="user-wrap">
    <div class="user">
      <img :src="user.avatar" alt class="avatar">
      <div class="message">
        <p class="username">{{user.username}}</p>
        <p class="total">文章：{{total}}</p>
      </div>
    </div>
    <ul class="blogs">
      <li v-for="blog in blogs" :key="blog.id">
        <div class="time">
          <p class="day">{{timeCompute(blog.createdAt).day}}</p>
          <p class="month">{{timeCompute(blog.createdAt).month}}月</p>
          <p class="year">{{timeCompute(blog.createdAt).year}}</p>
        </div>
        <div class="blog">
          <h2>{{blog.title}}</h2>
          <p>{{blog.description}}</p>
        </div>
      </li>
    </ul>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      :current-page="page"
      @current-change="changePage"
    ></el-pagination>
  </div>
</template>
<script>
import blog from '../api/blog.js'
export default {
  name: '',
  data() {
    return {
      blogs: [],
      user: {},
      total: null,
      page: 1
    }
  },
  created() {
    this.page = parseInt(this.$route.query.newPage) || 1
    this.fetchBlogs({ page: this.page })
  },
  computed: {},
  methods: {
    fetchBlogs({ page = 1 } = { page: 1 }) {
      blog
        .getBlogLists({ userId: this.$route.params.userId, page })
        .then(res => {
          console.log(res)
          this.blogs = res.data
          this.user = res.data[0].user
          this.total = res.total
        })
    },
    timeCompute(date) {
      let time = typeof date === 'object' ? date : new Date(date)
      return {
        year: time.getFullYear(),
        month: time.getMonth() + 1,
        day: time.getDate()
      }
    },
    changePage(newPage) {
      this.page = newPage
      this.fetchBlogs({ page: newPage })
      this.$router.push({ path: `/user/${this.user.id}`, query: { newPage } })
    }
  }
}
</script>
<style lang='less' scoped>
.user-wrap {
  width: 75%;
  margin: 0 auto;
  .user {
    display: flex;
    img {
      border-radius: 50%;
      margin-right: 20px;
    }
    .username {
      font-size: 20px;
      font-weight: 700;
      margin-bottom: 8px;
    }
    .total {
      color: #888888;
    }
  }
  .blogs {
    li {
      display: flex;
      margin: 15px 10px;
      .time {
        margin-right: 15px;
        p {
          padding: 1px 5px;
          text-align: center;
          color: #888888;
          &:first-child {
            color: #007cdc;
            font-size: 35px;
          }
        }
      }
      .blog {
        h2 {
          padding: 7px 0;
        }
        p {
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
      }
    }
  }
  .el-pagination {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
  }
}
</style>




