<template>
  <div class="index">
    <ul>
      <li v-for="blog in blogs">
        <div class="left">
          <router-link :to="`/details/${blog.id}`">
            <h2>{{blog.title}}</h2>
          </router-link>
          <span class="time">{{$timeFilter(blog.createdAt)}}</span>
          <p>{{blog.description}}</p>
        </div>
        <div class="right">
          <router-link :to="`/user/${blog.user.id}`">
            <div class="avatar">
            <img :src="blog.user.avatar" alt>
            </div>
          </router-link>
          <router-link :to="`/user/${blog.user.id}`">
            <span>{{blog.user.username}}</span>
          </router-link>
        </div>
      </li>
    </ul>
    <div class="pagination">
      <el-pagination
        :pager-count='5'
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="page"
        @current-change="changePage"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
import fetchBlog from '../api/blog.js'
export default {
  name: '',
  data() {
    return {
      blogs: [],
      total: null,
      page: 1
    }
  },
  created() {
    this.page = parseInt(this.$route.query.currentPage, 10) || 1
    this.getBlogLists({ page: this.page })
  },
  methods: {
    getBlogLists({ page = 1 } = { page: 1 }) {
      fetchBlog.getBlogLists({ atIndex:true, page }).then(res => {
        res.data = res.data.filter(blog=> blog.user)
        this.blogs = res.data
        this.total = res.total
        this.page = res.page
      })
    },
    changePage(newPage) {
      this.getBlogLists({ page: newPage })
      this.$router.push({ path: '/', query: { currentPage: newPage } })
    }
  }
}
</script>
<style lang="less" scoped>
@import '../assets/base.less';
.pagination {
  margin: 40px 0 20px;
  text-align: center;
}

li {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  width: 60%;
  .left {
    width: 85%;
    padding-bottom: 10px;
    border-bottom: 1px solid #f0f0f0;
    h2 {
      display: inline-block;
      margin-right: 10px;
      margin: 10px 0;
    }
    p {
      color: #777;
      overflow : hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    .time {
      color: @fontGrayColor;
      font-size: 13px;
    }
  }
  .right {
    width: 10%;
    text-align: center;
    color: @fontGrayColor;
    img {
      width: 30px;
      border-radius: 50%;
    }
  }
}
@media (max-width: 500px) {
.index {
  margin-top: 15px;
}
li {
  width: 90%;
  .left {
    width: 75%;
    .time {
      font-size: 10px;
    }
    p {
      font-size: 12px;
    }
    h2 {
      font-size: 14px;
      margin: 2px 0;
    }
  }
  .right {
    width: 19%;
  } 
}
}
</style>



