<template>
  <div class="details">
    <h1>{{title}}</h1>
    <div class="message">
      <div class="left">
        <router-link :to="`/user/${user.id}`">
          <img :src="user.avatar" alt>
        </router-link>
      </div>
      <div class="right">
        <router-link :to="`/user/${user.id}`">{{user.username}}</router-link>
        <p class="time">{{$timeFilter(createdAt)}}</p>
      </div>
    </div>
    <div class="content">{{content}}</div>
  </div>
</template>
<script>
import blog from '../api/blog.js'
export default {
  name: '',
  data() {
    return {
      title: '',
      content: '',
      user: {},
      createdAt: ''
    }
  },
  created() {
    const blogId = this.$route.params.blogId
    this.fetchBlog(blogId)
  },
  methods: {
    fetchBlog(blogId) {
      blog.getBlogDetail(blogId).then(res => {
        this.title = res.data.title
        this.content = res.data.content
        this.user = res.data.user
        this.createdAt = res.data.createdAt
      })
    }
  }
}
</script>
<style lang="less" scoped>
.details {
  margin: 0 auto;
  width: 75%;
  .message {
    display: flex;
    margin: 18px 0;
    .left {
      margin-right: 16px;
      img {
        border-radius: 50%;
        width: 50px;
      }
    }
    .right {
      a {
        font-size: 20px;
      }
      p {
        color: #888888;
      }
    }
  }
  .content {
    line-height: 2;
    font-size: 16px;
  }
}
</style>



